.get-in-touch {
    background-image: url('../../assets//backgroundGraphic/Bottom\ Graphic.jpg');
    background-size: cover;
    background-position-x:-15vw;
    background-repeat: no-repeat;
    background-attachment: fixed;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    .form {
        margin-right: 6em;
        padding: 2em;
    }
    .wide {
        // margin-right: 4em;
        width: 320px;
        font-family: 'Poiret One', cursive;
        font-weight: 200;
        text-align: center;
    }
    .head {
        font-size: 40px;
        color: grey;
        margin: 0 0 10px 0;
    }
    .sub-head {
        font-size: 26px;
        margin: 0 0 50px 0;
        color: tomato;
    }
    .input-box{
        display: flex;
        flex-direction: column;
        justify-content: center;
        .border0 {
            border: none;
            box-shadow: 0px 0px 1px grey;
            // box-shadow: -1px -1px 5px white;
            margin: 5px 0;
            padding: 0 5px;
            height: 30px;
            width: 320px;   
            font-family: 'Poiret One', cursive;
            font-size:14px ;            
        }       
        :placeholder-shown {
            font-family: 'Poiret One', cursive;
            font-size:14px ;
        }
        .message-box {
            height: 120px;
            padding: 5px;
        }
        .button {
            height: 30px;
            width: 100px;
            color: white;
            border: none;
            background-color: rgb(99, 201, 241);
            margin: 10px 0 0 0;
            font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
            font-weight: bolder;
            letter-spacing: 1px;

            // border: none;
            // box-shadow: 4px 4px 6px gray;
        }
    }
   
}

@media screen and (max-width:800px) {
    .get-in-touch {
        align-items: center;
        .form {
            margin-right: 0;
            padding: 2em  0;
        }
    }
}
