.projects-page {
    margin-top: 70px;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    animation-name:floatfade ;
    animation-duration: 0.5s;
    .cell {
        margin:0 0 0 0;
    }
    @keyframes floatfade {
        0% {margin-top:0px;opacity: 0%;}
        100% {margin-top:70px;opacity: 100%;}
    }
}
